:root {
  --bg: #0f0a1f; /* deep eggplant */
  --panel: rgba(255, 255, 255, 0.06);
  --border: rgba(255, 255, 255, 0.12);
  --chip: rgba(255, 255, 255, 0.08);
  --chip-hover: rgba(255, 255, 255, 0.16);
  --text: #e8eef7;
  --muted: #a7b2c3;
  --accent: #7cf2c6;
  --accent-2: #7cb8ff;
}

.coursework {
  position: relative;
  isolation: isolate;
  background: radial-gradient(
      1200px 600px at 10% -10%,
      rgba(138, 43, 226, 0.22),
      transparent 60%
    ),
    /* purple glow */
      radial-gradient(
        900px 500px at 100% 0%,
        rgba(156, 163, 175, 0.14),
        transparent 60%
      ),
    /* soft gray wash */ var(--bg);
  color: var(--text);
  padding: clamp(3rem, 6vw, 5rem) min(6vw, 3rem);
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.35) inset;
}

.coursework__header {
  max-width: 880px;
  margin: 0 auto 2.25rem auto;
  text-align: center;
}

.coursework__header h2 {
  font-size: clamp(1.8rem, 3.2vw, 2.4rem);
  line-height: 1.15;
  letter-spacing: 0.4px;
  margin: 0 0 0.5rem 0;
  background: linear-gradient(90deg, var(--accent), var(--accent-2));
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
}

.coursework__header p {
  margin: 0;
  color: var(--muted);
  font-size: clamp(0.95rem, 1.6vw, 1.05rem);
}

.coursework__grid {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: clamp(0.9rem, 1.8vw, 1.25rem);
  max-width: 1100px;
  margin: 2rem auto 0 auto;
}

/* Responsive spans */
.coursework__card {
  grid-column: span 12;
}
@media (min-width: 780px) {
  .coursework__card {
    grid-column: span 6;
  }
}
@media (min-width: 1040px) {
  .coursework__card {
    grid-column: span 4;
  }
}

.coursework__card {
  position: relative;
  background: linear-gradient(
    180deg,
    rgba(255, 255, 255, 0.08),
    rgba(255, 255, 255, 0.04)
  );
  border: 1px solid var(--border);
  border-radius: 16px;
  padding: 1.1rem clamp(1rem, 2vw, 1.25rem);
  backdrop-filter: blur(6px);
  transition: transform 180ms ease, border-color 180ms ease,
    box-shadow 180ms ease;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.25);
  overflow: hidden;
  text-align: center;
}
.coursework__card::after {
  content: "";
  position: absolute;
  inset: 0;
  pointer-events: none;
  background: radial-gradient(
    600px 120px at 0% 0%,
    rgba(124, 242, 198, 0.1),
    transparent 50%
  );
  opacity: 0.6;
}

.coursework__card:hover {
  transform: translateY(-3px);
  border-color: rgba(124, 184, 255, 0.35);
  box-shadow: 0 16px 26px rgba(0, 0, 0, 0.32);
}

.coursework__card-header {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.6rem;
  margin-bottom: 0.8rem;
}

.coursework__card-title {
  font-size: 1.05rem;
  font-weight: 700;
  letter-spacing: 0.3px;
}
.coursework__card-icon {
  font-size: 1.3rem;
  color: var(--accent);
  transition: transform 0.3s ease;
}

.coursework__card:hover .coursework__card-icon {
  transform: scale(1.1);
}

.coursework__chips {
  display: flex;
  flex-wrap: wrap;
  gap: 0.55rem;
  justify-content: center;
}

.coursework__chip {
  display: inline-flex;
  align-items: center;
  gap: 0.4rem;
  border: 1px solid var(--border);
  background: var(--chip);
  padding: 0.45rem 0.65rem;
  border-radius: 999px;
  font-size: 0.92rem;
  color: var(--text);
  transition:
    min-width 0.7s cubic-bezier(.77,0,.18,1),
    max-width 0.7s cubic-bezier(.77,0,.18,1),
    background 0.2s,
    box-shadow 0.25s;
  user-select: none;
  will-change: transform;
  line-height: 1;
  white-space: nowrap;
}
.coursework__chip:hover {
  background: var(--chip-hover);
  border-color: rgba(124, 242, 198, 0.35);
  transform: translateY(-1px);
}
.coursework__chip:active {
  transform: translateY(0);
}
.coursework__chip-wrapper {
  position: relative;
  display: inline-block;
}
.coursework__chip--expanded {
  background: var(--chip-hover);
  border-color: rgba(124, 242, 198, 0.35);
  min-width: 220px;
  max-width: 340px;
  white-space: normal;
  padding-right: 1rem;
  transition:
    min-width 0.7s cubic-bezier(.77,0,.18,1),
    max-width 0.7s cubic-bezier(.77,0,.18,1),
    background 0.2s,
    box-shadow 0.25s;
  box-shadow: 0 0 0 0.18rem var(--accent-2, #7cb8ff);
}
.coursework__chip-desc {
  display: inline;
  margin-left: 0.5rem;
  vertical-align: middle;
}

